@use "../../styles/mixins.scss";

.navContainer{
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
}

.nav {
    max-width: 1158px;
    width: 100%;
    // margin: 0 auto;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 37px;
    padding: 27px 60px;
    position: absolute; 
    border-radius: 25px; 
    background-color: white; 
  }

  .nav::before {
    content: '';
    position: absolute;
    top: -1px;
    left: -1.5000px;
    right: -1px;
    bottom: -1px;
    background: linear-gradient(180deg, #B5179E 0%, #850AD6 100%); 
    border-radius: 25px; 
    z-index: -1; 
    // padding: 5px; 
  }

.navList{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    list-style-type: none;
}

.navListItem{
    @include mixins.textNavigationStyling;
}

